<template>
  <v-card class="pa-4">
    <h2>Outstanding Assignment (By Branch)</h2>
    <v-row dense>
      <v-col cols="12" md="4">
        <v-menu v-model="menuMonth" :close-on-content-click="false">
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="selectedMonth"
              label="Month"
              prepend-icon="mdi-calendar"
              readonly
              v-bind="attrs"
              v-on="on"
              dense
              required
            />
          </template>
          <v-date-picker
            v-model="selectedMonth"
            type="month"
            @change="menuMonth = false"
          />
        </v-menu>
      </v-col>
      <v-col cols="12" md="4" class="d-flex align-end">
        <v-btn
          color="primary"
          @click="generateReport"
          :disabled="!selectedMonth"
        >
          Generate Report
        </v-btn>
      </v-col>
    </v-row>
    <v-divider class="my-4" />
    <div v-if="reportData.length">
      <v-simple-table>
        <thead>
          <tr>
            <th>INSURER</th>
            <th>WITHIN TAT</th>
            <th>TAT BREACH</th>
            <th>TOTAL</th>
            <th>BREACH PERCENTAGE</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="row in reportData" :key="row.branch">
            <td>{{ row.branch }}</td>
            <td>{{ row.withinTat }}</td>
            <td>{{ row.breachedTat }}</td>
            <td>{{ row.total }}</td>
            <td>
              {{
                row.total > 0
                  ? ((row.breachedTat / row.total) * 100).toFixed(2)
                  : "0.00"
              }}
            </td>
          </tr>
          <tr>
            <td><strong>Total</strong></td>
            <td>
              <strong>{{ totals.withinTat }}</strong>
            </td>
            <td>
              <strong>{{ totals.breachedTat }}</strong>
            </td>
            <td>
              <strong>{{ totals.total }}</strong>
            </td>
            <td>
              <strong>
                {{
                  totals.total > 0
                    ? ((totals.breachedTat / totals.total) * 100).toFixed(2)
                    : "0.00"
                }}
              </strong>
            </td>
          </tr>
        </tbody>
      </v-simple-table>
      <v-btn
        color="success"
        class="mt-4"
        @click="exportExcel"
        :loading="exporting"
      >
        <v-icon left>mdi-file-excel</v-icon>
        Export to Excel
      </v-btn>
    </div>
    <v-alert v-else-if="noDataMessage" type="warning" outlined class="mt-4">
      {{ noDataMessage }}
    </v-alert>
    <v-alert v-else type="info" outlined class="mt-4">
      Please select month and click "Generate Report" to view data.
    </v-alert>
  </v-card>
</template>

<script>
import axios from "axios";
import moment from "moment";
import { apiConfig } from "../../config/api.config";

export default {
  name: "OutstandingBranch",
  data() {
    return {
      selectedMonth: null,
      menuMonth: false,
      reportData: [],
      totals: {
        withinTat: 0,
        breachedTat: 0,
        total: 0,
      },
      exporting: false,
      noDataMessage: "",
    };
  },
  methods: {
    async generateReport() {
      this.reportData = [];
      this.totals = { withinTat: 0, breachedTat: 0, total: 0 };
      this.noDataMessage = "";
      try {
        const params = {
          month: moment(this.selectedMonth).format("YYYY-MM"),
        };
        const res = await axios.get(
          `${apiConfig.baseURL}/compliance/outstanding-branch`,
          { params }
        );
        this.processReportData(res.data);
        if (
          !Array.isArray(res.data.reportData) ||
          res.data.reportData.length === 0
        ) {
          const monthStr = moment(this.selectedMonth).format("MMMM YYYY");
          this.noDataMessage = `No outstanding data found for ${monthStr}.`;
        }
      } catch (err) {
        this.noDataMessage =
          err.message || "Failed to fetch report data. Please try again.";
      }
    },
    processReportData(data) {
      this.reportData = Array.isArray(data.reportData) ? data.reportData : [];
      const totals = { withinTat: 0, breachedTat: 0, total: 0 };
      this.reportData.forEach((row) => {
        totals.withinTat += row.withinTat;
        totals.breachedTat += row.breachedTat;
        totals.total += row.total;
      });
      this.totals = totals;
    },
    async exportExcel() {
      this.exporting = true;
      try {
        const params = {
          month: moment(this.selectedMonth).format("YYYY-MM"),
        };
        const url =
          `${apiConfig.baseURL}/compliance/outstanding-branch/export?` +
          new URLSearchParams(params).toString();
        window.open(url, "_blank");
      } catch (err) {
        this.$emit("error", err.message || "Failed to export Excel");
      } finally {
        this.exporting = false;
      }
    },
  },
};
</script>
